.photosMainBox {
  height: 100%;
  background-color: white;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  overflow: scroll;
  -webkit-transition: 0.7s;
  transition: 0.7s;
}

@media (min-width: 1200px) {
  .photosMainBox {
    height: auto;
  }
}

.photoMain {
  height: 100%;
  /* width: 100%; */
  padding: 0px 10px 0px 0px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  overflow: scroll;
}

.kitBox {
  height: 90%;
  width: 0px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  -webkit-transition: 0.7s;
  transition: 0.7s;
  overflow: scroll;
}

.aboutBox {
  height: 90%;
  width: 0px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  -webkit-transition: 0.7s;
  transition: 0.7s;
  overflow: scroll;
}

.contactBox {
  height: 90%;
  width: 0px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  -webkit-transition: 0.7s;
  transition: 0.7s;
  overflow: scroll;
  font-family: "Playfair Display", serif;
  align-items: flex-end;
  padding-bottom: 100px;
}

.kitPics {
  width: 250px;
  margin: 10px;
}

/* Contact */

.socialImages {
  height: 50px;
  width: auto;
  margin: 20px;
}

.contactHolder {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border: solid black;
  border-radius: 10px;
}

.contactHolder a {
  text-decoration: none;
  color: black;
}

.socialAll {
  min-width: 100%;
  display: flex;
  flex-direction: row;
}

.albumsBox {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  -webkit-transition: 0.7s;
  transition: 0.7s;
}

.albumsTabs {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.albumMenuItems {
  /* min-height: 300px; */
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  font-family: "Playfair Display", serif;
  cursor: default;
}

.albumMenuItems a {
  color: black;
  text-decoration: none;
}

.albumMenuItems a:hover {
  color: black;
  text-decoration: none;
  opacity: 0.5;
  -webkit-transition: 300ms;
  transition: 300ms;
}

@media (min-width: 768px) {
  .albumsBox {
    height: 90vh;
  }
  .albumMenuItems {
    height: 300px;
    width: 200px;
  }
  .albumMenuItems {
    -webkit-transition: 0.7s;
    transition: 0.7s;
    width: 150;
  }
}

.menuHolder {
  -webkit-transition: 1s;
  transition: 1s;
}

.mainMenu {
  background-color: white;
}

/* Logo */

.logo {
  width: 125px;
}

.logoContainer {
  font-family: "Yeseva One", cursive;
  font-size: 2em;
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;
}

.menuItems {
  min-width: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  font-family: "Playfair Display", serif;
  cursor: default;
  -webkit-transition: 1s;
  transition: 1s;
  color: black;
  text-decoration: none;
}

/* .menuItems {
  transition: 0.5s;
} */

.menuItem {
  font-size: small;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  color: black;
  text-decoration: none;
}

.menuItem:hover {
  opacity: 0.5;
  color: black;
  text-decoration: none;
}

@media (min-width: 0px) {
  .logo {
    width: 75px;
  }
}

@media (min-width: 576px) {
  .logo {
    width: 150px;
  }
}

@media (min-width: 768px) {
  .mainMenu {
    height: 90vh;
  }
  .logo {
    width: 200px;
  }
  .menuItems {
    max-height: 300px;
  }
  .menuItem {
    /* font-size: 1.5em; */
  }
}

